body {
  background-color: #EEEBD3;
}
.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card-img {
  /* flex-direction: row; */
  width:100%;
  height: 300px;
  border-radius: 25px;
  

}

.card{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
  padding: 60px;
  
  background-color: #D0D6B3;
  box-shadow: 10px 10px #AAAE7F
  
  
}
.flex-container{
  display: inline-flex;
  flex-direction: row;
  justify-content:  space-evenly;
  padding-left: 50px;
  padding-right: 40px;
  padding-top: 20px;
  padding-bottom: 30px;
  
  
  


  
}

.center {
  text-align: center;
  display: block;
  justify-content: center;

  
  align-items: center;
  margin: auto;
  /* width: 100%; */
}

.prod-container{
  display: inline-flex;
  flex-direction: row;
  justify-content:  space-evenly;
  width:100%;
  
  
}

.prod-card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  max-width: 300px;
  margin: auto;
  text-align: center;
  font-family: arial;
  background-color: #D0D6B3;
  padding-bottom: 10%;
  box-shadow: 10px 10px #AAAE7F
  
  
  
  
  

  /* width: 95%;
  height: 300px; */
  /* border-radius: 25px; */
}

.prod-img{
  width:100%;
}

/* Review Styling (dique) */

.review-container{
    border-radius: 5px;
    background-color: #D0D6B3;
    /* background-color: #f2f2f2; */
    padding: 20px;
    max-width: 540px;

    text-align: center;
    display: block;
    justify-content: center;

  
    align-items: center;
   margin: auto;
}

input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;


  
}


/* NavBar Styling (dique) */

.topnav {
  background-color: rgb(147, 149, 129);
  overflow: hidden;
}

.nav-button {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.nav-button:hover {
  background-color: #ddd;
  color: black;
}

.nav-button.active {
  background-color: goldenrod;
  color: white;
}

/* Testimonial styling  */

.test-container {
  
    border: 2px solid black;
    background-color: #EEEBD3;
    border-radius: 5px;
    padding-left: 16px;
    margin: 16px 0;
    /* width: 50%; */

    text-align: center;
    
    /* display: block;
    justify-content: center;
  
    
    align-items: center;
    margin: auto; */
  
}

.test-container::after {
  content: "";
  clear: both;
  display: table;
  /* padding-left: 10%; */
}

.test-container span {
  font-size: 20px;
  margin-right: 15px;
}


/* Profile Card styling */

.profile-card {
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); */
  padding: 40px;
  max-width: 300px;
  margin: auto;
  text-align: center;
  background-color: #D0D6B3;
  box-shadow: 10px 10px #AAAE7F
  
  
}

.prof-title {
  color: grey;
  font-size: 18px;
}

.profbutton {
  border: none;
  outline: 0;
  display: inline-block;
  padding: 8px;
  color: white;
  background-color: #000;
  text-align: center;
  cursor: pointer;
  width: 100%;
  font-size: 18px;
}

a {
  text-decoration: none;
  font-size: 22px;
  color: black;
}

.profbutton:hover, a:hover {
  opacity: 0.7;
}

.propic{
  width:100%;
  height: 300px;
  border-radius: 25px;  
}

