/* Create two columns that float next to eachother */
.column {
  float: left;
  width: 50%;
  margin-top: 6px;
  padding: 10px;
  
}

.row:after {
  content: "";
  display: table;
  clear: both;
  /* background-color: #D0D6B3; */
}

.contact-form{
  background-color: #D0D6B3;
  text-align: center;
 display: block;
 justify-content: center;

 
 align-items: center;
 margin: auto;

  
  
}

.contact-form, #input[type=text], select {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  margin-top: 6px;
  margin-bottom: 19px;
  resize: vertical;
  /* background-color: #D0D6B3; */
}
.contact-form, textarea{
  width: 96%;
  padding: 12px;
  border: 1px solid #ccc;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
  /* background-color: #D0D6B3; */

}

/* Book Now Button */
.book-butt{
  background-color: #ddd;
  border: none;
  color: black;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 16px;
}

.glow {
  font-size: 80px;
  color: olivedrab;
  text-align: center;
  -webkit-animation: glow 1s ease-in-out infinite alternate;
  -moz-animation: glow 1s ease-in-out infinite alternate;
  animation: glow 1s ease-in-out infinite alternate;

}

@keyframes glow {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e69600, 0 0 40px #e69600, 0 0 50px #e69600, 0 0 60px #e69600, 0 0 70px #e60073;
  }
  to {
    text-shadow: 0 0 20px #fff, 0 0 30px green, 0 0 40px green, 0 0 50px green, 0 0 60px green, 0 0 70px green, 0 0 80px green;
  }
}


